<template>
 <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
      </crumb>
    </div>
    <div class="center_content">
        <!-- <div class="center_content_title">
            <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
            <span>{{pageTitle}}</span>
        </div> -->
        <div class="center_content_search">
            <div class="center_content_search_left">
                <div class="search-input-suffix gap">
                    <label>账号角色：</label>
                    <el-select v-model="page.roleID" placeholder="请选择">
                        <el-option
                        v-for="item in userProgramOptions"
                        :key="item.id"
                        :label="item.label"
                        :value="item.id">
                        </el-option>
                    </el-select>
                </div>
                <div class="search-input-suffix">
                    <label>用户搜索：</label>
                    <el-input class="search"
                            @keyup.enter.native="search"
                            v-model="page.keywords"
                            placeholder="请输入搜索关键词"
                            clearable
                            >
                    </el-input>
                    <!-- <el-button  @click="search" type="primary">搜索</el-button> -->
                </div>
                <div class="search-input-suffix" style="margin: 0 0 0 20px">
                  <div class="search--btn"  @click="search">搜索</div>
                </div>
            </div>
            <!-- <div class="center_content_search_right">
                <el-button @click="openAdd" type="primary" icon="el-icon-plus" round>新增</el-button>
            </div> -->
        </div>
         <div class="table-container">
             <el-table
                :data="tableData"
                stripe
                style="width: 100%">
                <el-table-column
                prop="realName"
                label="姓名">
                </el-table-column>
                <el-table-column
                prop="userName"
                label="登录名">
                </el-table-column>
                <el-table-column
                prop="rolesVal"
                label="角色">
                </el-table-column>
                <el-table-column
                prop="createDate"
                label="创建时间">
                </el-table-column>
                <el-table-column
                prop="creatorVal"
                label="创建人">
                </el-table-column>
                <el-table-column
                label="操作"
                width="234">
                <template slot-scope="scope">
                    <div class="operate-button distance">
                        <SvgIcon icon-class="iconbianji1"></SvgIcon>
                        <el-button
                        @click.native.prevent="editRow(scope.row)"
                        type="text"
                        size="small">
                        编辑
                        </el-button>
                    </div>
                    <div class="operate-button">
                        <SvgIcon icon-class="iconshanchu"></SvgIcon>
                        <el-button
                        @click.native.prevent="deleteRow(scope.row)"
                        type="text"
                        size="small"
                        style="color: #ff5858">
                        删除
                        </el-button>
                    </div>                    
                </template>
                </el-table-column>
            </el-table>
            <div class="center_pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.rows"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="data.total">
                </el-pagination>
            </div>
        </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog :title="dialogTitle" :visible.sync="userDialogFormVisible" :before-close="handleDialogClose" class="set-el-dialog" :close-on-click-modal='false'>
        <el-form :model="form" :rules="rules" ref="form" label-width="120px">
            <el-form-item label="用户名" prop="userName">
            <el-input v-model="form.userName" :disabled="!isAdd" placeholder=""></el-input>
            </el-form-item>
            <!-- <el-form-item label="密码" prop="password">
            <el-input v-model="form.password"  placeholder=""></el-input>
            </el-form-item> -->
             <el-form-item label="性别" prop="gender">
             <el-radio-group v-model="form.gender">
                <el-radio :label="0">男</el-radio>
                <el-radio :label="1">女</el-radio>
            </el-radio-group>
            </el-form-item>
             <el-row>
                <el-col :span="12">
                    <el-form-item label="用户角色" prop="roleIDs">
                        <el-select v-model="form.roleIDs" multiple placeholder="请选择">
                            <el-option
                                v-for="item in roleOptions"
                                :key="item.id"
                                :label="item.label"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="所属学院" prop="academyId">
                        <el-select v-model="form.academyId"  placeholder="请选择">
                            <el-option
                                v-for="item in roleOptions1"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <!-- <el-col :span="12">
                    <el-form-item label="所属专业" prop="specialtyId">
                        <el-select v-model="form.specialtyId"  placeholder="请选择">
                            <el-option
                                v-for="item in roleOptions2"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col> -->
            </el-row>
             <el-form-item label="真实姓名" prop="realName">
            <el-input v-model="form.realName"  placeholder=""></el-input>
            </el-form-item>
             <el-form-item label="手机号" prop="phone">
            <el-input v-model="form.phone"  placeholder=""></el-input>
            </el-form-item>
             <el-form-item label="联系邮箱" prop="email">
            <el-input v-model="form.email"  placeholder=""></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button  @click="cancel('form')">取 消</el-button>
            <el-button type="primary" @click="create('form')">确 定</el-button>
        </div>
    </el-dialog>
 </div>
</template>

<script>
import systemApi from '@/api/systemApi'
export default {
    name: 'UserManagement',
    data() {
        const checkPassword = (rule, value, callback) => {
            const  reg = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,20}$/
                if (!value) {
                    callback(new Error('设置设置6至20位登录密码'))
                } 
                if(!reg.test(value)){
                    callback(new Error('密码6至20位，必须包含字母与数字'))
                }else{
                    callback()
                }
            };
        const checkPhone = (rule, value, callback) => {
            const reg=/^1[358][0-9]{9}$/;
            if (!value) {
                callback();
            }
            if (!reg.test(value)) {
                callback(new Error('请输入正确的手机号'));
            } else {
                callback();
            }
        };
        const checkEmail = (rule, value, callback) => {
            const reg = /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/;
            if (!value) {
                callback();
            }
            if (!reg.test(value)) {
                callback(new Error('请输入正确的邮箱地址'));
            } else {
                callback();
            }
        };
        return {
            pageTitle:'用户管理',
            isAdd:true,
            userDialogFormVisible:false,
            data:{},
            userProgramOptions: [],
            roleOptions:[],
            roleOptions1:[],
            roleOptions2:[],
            tableData: [],
            form:{
                userName:'',
                // password:'',
                gender:0,  //0 男性 1 女性
                roleIDs:[],
                academyId:'',
                // specialtyId:'',
                realName:'',
                phone:'',
                email:'',
                id:undefined
            },
            page:{
                roleID:'',
                keywords:'',
                page:1,
                rows:15
            },
            rules:{
                userName: [
                    {
                    required: true,
                    message: '请输入用户名',
                    trigger: 'blur'
                    }
                ],
                password: [
                    { required: true,  validator: checkPassword, trigger: 'blur' }
                ],
                phone: [
                    {
                    validator: checkPhone,
                    trigger: 'blur'
                    }
                ],
                email: [
                    {
                    validator: checkEmail,
                    trigger: 'blur'
                    }
                ]
            }
        }
    },
    created(){},
    mounted(){
        this.initData()
        this.sysRoleAll()
        this.allDeptments()
    },
    computed:{
        dialogTitle(){
            return this.isAdd ? '添加用户' : '编辑用户'
        }
    },
    methods:{
        initData(){
            const url =systemApi.sysUserList;
            this.$post(url,this.page).then(res =>{
                if(res.status === 1){
                    this.data=res;
                    this.tableData=res.data;
                    
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }
            }).catch(err =>{
                console.log(err)
            })
        },
        sysRoleAll(){
            const url =systemApi.sysRoleAll;
            this.$get(url).then(res =>{
            if(res.status === 1){
            const allObj={
                id: '',
                label:'全部'
                } 
                this.roleOptions= JSON.parse(JSON.stringify(res.data))          
                this.userProgramOptions=res.data 
                this.userProgramOptions.unshift(allObj)           
            }else{
                this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.message
                })
            }
            }).catch(err =>{
                console.log(err)
            })
        },
        //   所属学院
        allDeptments(){
            this.$get(`/basic/academy/all?type=${0}`).then(res=>{
                if(res.status === 1){
                    this.roleOptions1= JSON.parse(JSON.stringify(res.data))
                    this.roleOptions1.unshift({
                      id: '0',
                      name: '无'
                    })
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }
            }).catch(err =>{
                console.log(err)
            })
        },
        // 关联学院搜索专业
        changeacademy(){
            this.$get(`/basic/specialty/all/${this.form.academyId}`).then(res=>{
                if(res.status === 1){
                    this.roleOptions2= JSON.parse(JSON.stringify(res.data))    
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }
            })
        },
        headInfo(item){
            this.pageTitle=item[1]
        },
        openAdd(){         
            this.userDialogFormVisible = true;
            this.form={
                userName:'',
                // password:'',
                gender:0,
                roleIDs:[],
                academyId:'',
                // specialtyId:'',
                realName:'',
                phone:'',
                email:'',
                id:undefined
            };
            this.isAdd=true;
        },
        search(){
            this.resetTemp();
            this.initData() ;
        },
        handleSizeChange(val){
            this.page.rows = val;
            this.initData()
        },
        handleCurrentChange(obj){
            this.page.page = obj;
            this.initData()
        },
        deleteRow(row){
            this.$confirm('确定删除该用户?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.userDel(row.id)
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });        
        },
        editRow(row){
            const obj={
                userName:row.userName,
                // password:row.password,
                gender:row.gender,
                roleIDs:row.roleIDs,
                academyId:row.academyId,
                // specialtyId:row.specialtyId,
                realName:row.realName,
                phone:row.phone,
                email:row.email,
                id:row.id
            }
            this.form=obj;
            this.userDialogFormVisible = true; 
            this.isAdd=false;
        },
        create(formName){             
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if(this.isAdd){
                        this.userAdd()
                    }else{   
                        this.userEdit()       
                    }           
                } 
            });
        },
        cancel(formName){
            this.userDialogFormVisible = false;
            this.$refs[formName].resetFields();
        },
        handleDialogClose(){
            this.$refs['form'].resetFields();
            this.userDialogFormVisible = false;    
        },
        userAdd(){
            const url =systemApi.sysUserAdd;
            this.$post(url,this.form).then( res => {
                if(res.status === 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: res.message
                        })
                    this.userDialogFormVisible = false;
                    this.resetTemp()
                    this.initData() ;
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }  
            }).catch(err =>{
                console.log(err)
            })
        },
        userEdit(){
            const url =systemApi.sysUserEdit;
            this.$put(url,this.form).then( res => {
                if(res.status === 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: res.message
                        
                        })
                    this.userDialogFormVisible = false;
                    this.initData() ;
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }
            }).catch(err =>{
                console.log(err)
            })
        },
        userDel(id){
            const url =systemApi.sysUserDel({id:id});
            this.$deletes(url).then( res => {
                if(res.status === 1){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: res.message
                        })
                    this.initData() ;
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }
            }).catch(err =>{
                console.log(err)
            })
        },
        resetTemp(){
            this.page.page=1;
            this.page.rows=15;
        }
    }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss'
</style>



